.MainDownSlideGroup {
    position: absolute;
    top: -10vh;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    height: 100vh;
    display: flex;
    margin: 0;
    align-items: center;
    justify-content: center;
    /*    */
    user-select: none;
    z-index: 900;
}

.MainDownSlideItem {
    width: 15vw;
    height: 40vh;
    background-position: center;
    background-size: cover;
    margin: 1vw;
    border-radius: 3vw;
    display: inline-block;
    cursor: pointer;
    /*filter: grayscale(100%);*/
}
.SelectedName{
    font-family: "Song Myung";
    font-size: 20px;
    font-weight: 600;
    font-style: italic; /*   */
    text-transform: uppercase; /*     */

}
