/*    */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

/*    */
.VerificationPage{
    background-color: #2B2B2B;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    text-align: center;
    color: #000000;
    position: absolute;
}

/*    */
.VerificationIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    top: 87px;
    left: 32px;
}

/*    */
.VerificationTitle {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 85px;
    left:142px;
}

/*    */
.VerificationHeaderText {
    position: absolute;
    width: 300px;
    height: 30px;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 13px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    top: 164px;
    left: 44px;
    text-align: left;
}

.ConfirmButton {
    border: none;
    background: transparent;
    padding: 0;

}

.ConfirmIcon {
    position: absolute;
    width: 30.86px;
    height: 30.86px;
    flex-shrink: 0;
    top: 48vh;
    left: 80%;
}

/*     */
.VerificationInputPhone {
    position: absolute;
    top: 30vh;
    left:50%;
    transform: translateX(-50%);
}
.VerificationInputCode{
    position: absolute;
    top: 45vh;
    left:50%;
    transform: translateX(-50%);
}

.VerificationInputPhone input,
.VerificationInputCode input{
    font-size: 25px; /*      */
    padding: 10px 10px 10px 5px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
    display: block;
    width: 340px;
    height: 70px;
    border: none;
    color: #ffffff;
    border-bottom: 1px solid #757575;
    outline: none; /*         */
}

.VerificationInputPhone input::placeholder,
.VerificationInputCode input::placeholder{
    text-align: center;
    font-size: 18px;
}

/*       */
.VerificationInputPhone label,
.VerificationInputCode label{
    color:#999;
    font-size:10px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
    opacity: 0;
}

.VerificationInputPhone img,
.VerificationInputCode img{
    position: absolute;
    left: 16px;
    opacity: 1; /*    */
    transition:0.2s ease all; /*      */
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}

/*           */
.VerificationInputPhone input:focus ~ img,
.VerificationInputPhone input:valid ~ img,
.VerificationInputCode input:focus ~ img,
.VerificationInputCode input:valid ~ img{
    opacity: 0;
}


.VerificationPassIcon{
    top: 23px;
}

.VerficationPhoneIcon{
    top: 21px;
}


/*              */
.VerificationInputPhone input:focus ~ label, input:valid ~ label,
.VerificationInputCode input:focus ~ label, input:valid ~ label{
    top:-30px;
    font-size:20px;
    color:#D6B4A2;
    opacity: 1;
}

/*      */
.VerificationBar {
    position: relative;
    display: block;
    width: 328px;
}

.VerificationBar:before,
.VerificationBar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #D6B4A2;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.VerificationBar:before {
    left: 50%;
}

.VerificationBar:after {
    right: 50%;
}

/*         */
input:focus ~ .VerificationBar:before,
input:focus ~ .VerificationBar:after {
    width: calc(50% - 3%);
}

/*        */
.VerificationHighlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/*     */
input:focus ~ .VerificationHighlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    -moz-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
}

/*    */
@keyframes VerificationHighlight {
    from {
        background: #D6B4A2;
    }
    to {
        width: 0;
        background: transparent;
    }
}

/*    */
.VerificationButton {
    position: absolute;
    top: 70vh;
    left:50%;
    transform: translateX(-50%);
    width: 340px;
    height: 70px;
    border-radius: 15px;
    background: #D6B4A2;
}

/*      */
.VerificationButton span {
    position: absolute;
    top: 21px;
    left: 134px;
    color: #2b2b2b;
    text-align: center;
    font-family: 'Roboto';
    font-size: 20px;
    font-weight: 600;
    line-height: normal;
}

