/*    */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

/*    */
.FindPassPage{
    background-color: #2B2B2B;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    text-align: center;
    color: #000000;
    position: absolute;
}

/*    */
.FindPassIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    top: 87px;
    left: 32px;
}

/*    */
.FindPasspTitle {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 85px;
    left:142px;
}

/*    */
.FindPassHeaderText{
    position: absolute;
    width: 300px;
    height: 30px;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 13px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    top: 164px;
    left: 44px;
    text-align: left;
}

/*   */
.FindPassPhoneButton {
    cursor: pointer;
    position: absolute;
    top: 30vh;
    left:50%;
    transform: translateX(-50%);
    width: 340px;
    height: 70px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
}

/*      */
.FindPhoneButtonText {
    position: absolute;
    top: 24px;
    left: 103px;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 20px;
    font-weight: 500;
    line-height: normal;
}

/*     */
.FindPhone{
    width: 16.25px;
    height: 27.5px;
    flex-shrink: 0;
    position: absolute;
    top: 21px;
    left: 19px;
}

.FindPassEmailButton{
    position: absolute;
    top: 45vh;
    left:50%;
    transform: translateX(-50%);
    width: 340px;
    height: 70px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
}

.FindEmail{
    width: 22px;
    height: 17.864px;
    flex-shrink: 0;
    position: absolute;
    top: 26px;
    left: 16px;
}

.FindEmailButtonText {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: Roboto;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    top: 24px;
    left: 112px;
}

/*      */
.FindNaver {
    position: absolute;
    top: 60vh;
    left:20%;
}

/*      */
.FindGoogle {
    position: absolute;
    top: 60vh;
    left:50%;
    transform: translateX(-50%);
}

/*      */
.FindKakao {
    position: absolute;
    top: 60vh;
    left:70%;

}

/*     */
.FindPageFooter {
    position: absolute;
    top: 80vh;
    left: 50%;
    transform: translateX(-50%);
    font-size: 15px;
    font-weight: 600;
}

/*     */
.FindPageFooterText1 {
    color: #ffffff;
}

.FindPageFooterText2 {
    color: #2b2b2b;
}

.FindPageFooterText3 {
    text-decoration: underline;
    color: #D6B4A2;
}

