@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

/*      */
.LoginPage {
    background-color: #2b2b2b;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    text-align: center;
    color: #ffffff;
    position: absolute;
}

/*    */
.HeaderIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    stroke: #ffffff;
    top: 10vh;
    left: 10%;
}

/*    */
.HeaderTitle {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 10vh;
    left:50%;
    transform: translateX(-50%);
}

/*     */
.LoginPageInputEmail {
    position: absolute;
    top: 25vh;
    left:50%;
    transform: translateX(-50%);
}
.LoginPageInputPassword{
    position: absolute;
    top: 38vh;
    left:50%;
    transform: translateX(-50%);
}

.LoginPageInputEmail input,
.LoginPageInputPassword input{
    font-size: 25px; /*      */
    padding: 10px 10px 10px 5px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
    display: block;
    width: 340px;
    height: 70px;
    border: none;
    color: #ffffff;
    border-bottom: 1px solid #757575;
    outline: none; /*         */
}

.LoginPageInputEmail input::placeholder,
.LoginPageInputPassword input::placeholder{
    text-align: center;
    font-size: 18px;
}

/*       */
.LoginPageInputEmail label,
.LoginPageInputPassword label {
    color:#999;
    font-size:10px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
    opacity: 0;
}

.LoginPageInputEmail img,
.LoginPageInputPassword img{
    position: absolute;
    left: 16px;
    opacity: 1; /*    */
    transition:0.2s ease all; /*      */
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}

/*           */
.LoginPageInputEmail input:focus ~ img,
.LoginPageInputEmail input:valid ~ img,
.LoginPageInputPassword input:focus ~ img,
.LoginPageInputPassword input:valid ~ img{
    opacity: 0;
}


.passicon{
    top: 23px;
}

.emailicon{
    top: 26px;
}


/*              */
.LoginPageInputEmail input:focus ~ label, input:valid ~ label,
.LoginPageInputPassword input:focus ~ label, input:valid ~ label{
    top:-30px;
    font-size:20px;
    color:#D6B4A2;
    opacity: 1;
}

/*      */
.bar {
    position: relative;
    display: block;
    width: 328px;
}

.bar:before,
.bar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #D6B4A2;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.bar:before {
    left: 50%;
}

.bar:after {
    right: 50%;
}

/*         */
input:focus ~ .bar:before,
input:focus ~ .bar:after {
    width: calc(50% - 3%);
}

/*        */
.highlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/*     */
input:focus ~ .highlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    -moz-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
}

/*    */
@keyframes inputHighlighter {
    from {
        background: #D6B4A2;
    }
    to {
        width: 0;
        background: transparent;
    }
}

/*     */
.LoginPageCheck {
    position: absolute;
    top: 48%;
    left: 10%;
}

/*     */
.LoginPageLink {
    position: absolute;
    top: 48%;
    right: 10%;
}
.LoginPageLink a {
    text-decoration: none; /*   */
    color: inherit; /*      */
}

/*    */
.LoginButton {
    position: absolute;
    top: 60vh;
    left:50%;
    transform: translateX(-50%);
    width: 340px;
    height: 70px;
    border-radius: 15px;
    background: #D6B4A2;
}

/*      */
.LoginButton span {
    position: absolute;
    top: 21px;
    left: 136px;
    color: #2b2b2b;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 600;
    line-height: normal;
}

/*     */
.LoginPageFooter {
    position: absolute;
    top: 90vh;
    left: calc(50% - 106px);
    font-size: 15px;
    font-weight: 600;
}

/*      */
#naverIdLogin{
    visibility: hidden;
    z-index: 4;
}
.LoginNaver {
    position: absolute;
    top: 73vh;
    left: 20%;
    border-radius: 100%;
    z-index: 2;
}

/*      */
.LoginGoogle {
    position: absolute;
    top: 73vh;
    left:50%;
    transform: translateX(-50%);
}

/*      */
.LoginKakao {
    position: absolute;
    top: 73vh;
    left: 70%
}

/*     */
.LoginPageFooterText1 {
    color: #ffffff;
}

.LoginPageFooterText2 {
    color: #2b2b2b;
}

.LoginPageFooterText3 {
    text-decoration: underline;
    color: #D6B4A2;
}

